.main-nav__list {
  text-align: center;
  background-color: #283645;
  position: absolute;
  padding-top: 66px;
  width: 100%;
  z-index: 1;
  left: 0;
  top: 0;
  transform: translateY(-100%);
  transition: transform 0.2s;
}

/* Пункт меню */
.main-nav__item {
  background-color: #283645;
  padding: 15px 0;
  margin-top: 1px;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}

/* Ссылка меню */
.main-nav__link {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  word-wrap: break-word;
}
.main-nav__link:hover {
  color: #d22856;
}
.main-nav__link:active {
  color: rgba(210, 40, 86, 0.3);
}

/* Кнопка Бургер */
.main-nav__toggle {
  position: relative;
  width: 50px;
  height: 24px;
  display: block;
  cursor: pointer;
  background: none;
  color: #ffffff;
  border: none;
  padding: 0;
  z-index: 2;
}

.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 4px;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  transition: 0.1s;
}

.main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
}

.main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 8px 0 0 rgba(210, 40, 86, 0.3), 0 16px 0 0 rgba(210, 40, 86, 0.3);
}

@media (max-width: 1200px) {
  /* Кнопка "крестик" при открытом состоянии*/
  .main-nav__toggle.active::before, .main-nav__toggle.active::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -4px;
    width: 32px;
    height: 3px;
    background-color: #ffffff;
    transition: linear 0.1s;
    z-index: 2;
  }
  .main-nav__toggle.active::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav__toggle.active::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }
  .main-nav__toggle.active:hover::before, .main-nav__toggle.active:hover::after {
    background-color: #d22856;
  }
  .main-nav__toggle.active:active::before, .main-nav__toggle.active:active::after {
    background-color: rgba(210, 40, 86, 0.3);
  }

  .main-nav__list.active {
    transform: translateY(0);
  }
}
/* Планшет */
@media (min-width: 660px) {
  .main-nav__list {
    padding-top: 110px;
  }
}
/* Desktop */
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav__logo-wrapper {
    padding: 0;
  }

  .main-nav__list {
