  }

  .main-nav__item {
    background: none;
    padding: 0;
    margin-top: 0;
    box-shadow: none;
  }
  .main-nav__item:not(:last-child) {
    margin-right: 78px;
  }
  .main-nav__item--current {
    position: relative;
  }
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #ffffff;
  }

  .main-nav__link {
    display: block;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    padding: 13px 0;
    color: #ffffff;
    text-transform: capitalize;
  }
  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
