/* Cама секция */
.price {
  margin: 0 auto;
  padding: 109px 0 31px;
  background-color: #f2f2f2;
  position: relative;
  /* Делаем скошенный угол */
}
.price::after {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  width: 100%;
  height: 88px;
  background-image: url(../img/price/price-triangle.svg);
  background-size: 100% 25px;
  background-position: 0 0;
  background-repeat: no-repeat;
}

/* Начало стилей для обертки над таблицей */
.price__wrapper {
  line-height: 30px;
  text-transform: uppercase;
  overflow: hidden;
  width: 100vw;
}

/* Смещаем таблицу на моб версии чтоб создать нудный эффект */
.price__table {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  transition: left 0.4s linear;
}

.table-price {
  width: 835px;
  border-spacing: 0;
}

.table-price__title {
  position: relative;
  font-size: 18px;
  color: #ffffff;
  background-color: #283645;
  border-right: 2px solid #e5e5e5;
  padding: 12px 0 26px;
  overflow: hidden;
  /* Убираем первую ячейку */
}
.table-price__title:first-child {
  display: none;
  border-right: 0;
  border-bottom: 2px solid #e5e5e5;
  background-color: transparent;
}
.table-price__title:last-child {
  border-right-color: #283645;
}

.table-price__title--hit::before {
  /* Cчитываем с атрибута текст ХИТ и стилизуем */
  content: attr(data-hit-text);
  position: absolute;
  top: 17px;
  left: -26px;
  width: 105px;
  height: 25px;
  font-size: 14px;
  line-height: 30px;
  background-color: #d22856;
  transform: rotate(-45deg);
}

.table-price__tariff,
.table-price__value {
  display: block;
}

.table-price__tariff {
  font-weight: 700;
  font-size: 16px;
}

.table-price__value {
  font-weight: 300;
  font-size: 18px;
}

.table-price__cell {
  padding: 15px 18px 14px 22px;
  border-right: 2px solid #e5e5e5;
  border-bottom: 2px solid #e5e5e5;
  background-color: #ffffff;
}
.table-price__cell:first-child {
  display: none;
  border-left: 2px solid #e5e5e5;
}

.table-price__feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.table-price__feature::before {
  content: attr(data-feature-name);
}

.table-price__feature-value {
  /* Общие стили для иконок в таблице */
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  /* Сами иконки */
}
.table-price__feature-value--true {
  width: 31px;
  height: 23px;
  background-image: url(../img/price/yes.svg);
}
.table-price__feature-value--false {
  width: 27px;
  height: 27px;
  background-image: url(../img/price/no.svg);
}

.price__slider {
  text-align: center;
  padding-top: 30px;
}

.price__slider-dots {
  border: none;
  padding: 0;
  width: 6px;
  height: 6px;
  cursor: pointer;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.3);
}
.price__slider-dots:not(:last-child) {
  margin-right: 19px;
}
.price__slider-dots:hover {
  background-color: rgba(40, 54, 69, 0.6);
}
.price__slider-dots:active {
  background-color: rgba(40, 54, 69, 0.1);
}

.price__slider-dots-current {
  padding: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: transparent;
  border: 3px solid #283645;
}
.price__slider-dots-current:hover, .price__slider-dots-current:active {
  background-color: transparent;
}

/* Изменения таблицы на разных экранах */
@media (min-width: 660px) {
  .price {
    width: 100%;
    padding: 112px 0 65px;
  }
  .price::after {
    background-size: 100% 55px;
  }

  .price__wrapper {
    width: auto;
  }

  .price__table {
    position: static;
    left: auto;
    transform: none;
  }

  .table-price {
    width: 100%;
  }

  .table-price__title {
    padding: 10px 0 8px;
  }
  .table-price__title:first-child {
    display: table-cell;
  }

  .table-price__title--hit::before {
    width: 85px;
    top: 10px;
    left: -24px;
  }

  .table-price__cell {
    padding: 8px 15px;
    width: 25%;
  }
  .table-price__cell:first-child {
    display: table-cell;
    text-transform: none;
  }

  .table-price__feature {
