.form__user-comment-area {
  width: 100%;
  text-align: center;
  padding: 20px;
  font-size: 18px;
  line-height: 30px;
}

/* Обертка кнопки "Отправить форму" */
.form__button-wrapper {
  background-color: #ffffff;
}

.form__button--green {
  background-color: #2aab6d;
  font-size: 18px;
  font-weight: 700;
  border: none;
  color: #ffffff;
  text-transform: uppercase;
}
.form__button--green:hover {
  background-color: #1C9B5E;
}
.form__button--green:active {
  color: rgba(255, 255, 255, 0.3);
}
.form__button--green:disabled {
  background-color: #e5e5e5;
}

.form__button {
  width: 100%;
  padding: 23px 10px;
  margin-bottom: 46px;
  cursor: pointer;
  word-break: break-all;
}

.form__prompt {
  display: block;
  text-transform: uppercase;
  text-align: center;
  word-break: break-all;
}
.form__prompt span {
  color: #d22856;
}

/* Планшет */
@media (min-width: 660px) {
  .cover-main__title-form {
    font-size: 50px;
    width: 620px;
  }

  .form__title {
    padding: 80px 0 0;
    width: 490px;
    font-size: 18px;
    margin: 0 auto;
  }

  .form__title span {
    display: block;
    font-size: 18px;
  }

  .form__user {
    grid-template-columns: repeat(auto-fill, minmax(616px, 1fr));
    row-gap: 60px;
  }

  .form__wrapper {
    background-color: #ffffff;
    margin: 0;
  }
  .form__wrapper--name {
    order: 1;
  }
  .form__wrapper--progress {
    order: 4;
  }
  .form__wrapper--contact {
    order: 3;
  }
  .form__wrapper--os {
    order: 2;
  }
  .form__wrapper--comment {
    order: 5;
  }

  .form__user-item {
    gap: 56px;
    border: 2px solid #E5E5E5;
    border-radius: 1px;
    padding: 27px 62px 53px;
  }
  .form__user-item--name {
    border: none;
  }
  .form__user-item--contact {
    gap: 30px;
  }
  .form__user-item--name {
    padding: 0;
  }

  .form__user-item-title {
    padding: 0 25px;
    line-height: 30px;
  }
  .form__user-item-title--contact {
    display: block;
  }

  .form__user-label {
    display: flex;
    align-items: center;
    flex-grow: 1;
    column-gap: 20px;
    padding: 0;
  }

  .form__user-item-input {
    position: relative;
    display: flex;
    flex-direction: column;
    row-gap: 12px;
  }
  .form__user-item-input::after {
    content: "";
    width: 59px;
    height: 59px;
    right: 2px;
    top: 2px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 20px auto;
  }
  .form__user-item-input--tel::after {
    content: "";
    position: absolute;
    background-image: url(../img/icons/phone-icon.svg);
    background-color: #e5e5e5;
  }
  .form__user-item-input--email::after {
    content: "";
    position: absolute;
    background-image: url(../img/icons/mail-icon.svg);
    background-color: #e5e5e5;
  }

  .form__user-input {
    margin: 0;
  }

  .form__user-label--contact {
    order: 1;
  }

  .form__user-comment-area {
    min-width: 496px;
    margin: 0 auto;
  }

  .form__button-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    order: 6;
    margin: 0;
  }

  .form__button {
    width: 296px;
    margin: 0;
  }
}
/* Desktop */
@media (min-width: 1200px) {
  .form__user {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    gap: 54px 74px;
    align-items: center;
    margin: 0 auto;
    width: 1200px;
  }

  .form__title, .form__wrapper, .form__button-wrapper {
    grid-column: 1/-1;
  }

  .form__wrapper {
    padding: 0 130px;
  }
  .form__wrapper--name {
    grid-column: 1/2;
    padding: 0;
    padding-left: 130px;
  }
  .form__wrapper--os {
    grid-column: 2/3;
    padding: 0;
    padding-right: 130px;
  }

  .form__user-item--contact {
    flex-direction: row;
    padding: 44px 62px 13px 62px;
    column-gap: 74px;
  }

  .form__user-item-input {
    width: 100%;
  }

  .form__user-progress-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 35px;
  }

  .form__user-progress-item:nth-child(even) {
    order: 2;
  }
  .form__user-progress-item:nth-child(odd) {
    order: 1;
  }
  .form__user-progress-item:not(:last-child) {
    margin-bottom: 0;
  }

  .form__user-comment-area {
    text-align: left;
  }
}